.channel-category {
	background-color: #fcf7f2;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
	.cat-link-title {
		color: #888;
	}
	.cat-link {
		& > li {
			a {
				padding: 6px 14px;
				color: #fff;
				background-color: #ccc0b6;
				background-repeat: repeat;
				background-position: center;
				background-image: url(dir('common/bg_02.png'));
				border: 1px solid #dedede;
				transition: all 100ms ease-in;
				&:hover {
					text-decoration: none;
					color: $default-text-color;
					background-color: $default-color;
					border-color: $default-color;
				}
			}
			&.current {
				a {
					color: $default-text-color;
					background-color: $default-color;
					background-image: url(dir('common/bg_brown.png'));
					border-color: $default-color;
				}
			}
			&.channel-all {
				a {
					padding-left: 20px;
					padding-right: 20px;
				}
			}
		}
	}
}

.channel-loop {
	.title {
		color: #695143;
		border-bottom: 1px solid #e2dad6;
	}
}
