// Sidebar ---*/
.sidebar {
	.widget {
		.thumb {
			.not-thumb {
				background-color: #f0e6dc;
				&:before {
					color: darken(#f0e6dc, 10%);
				}
			}
		} // .thumb
	} // .widget
	.widgettitle {
		border-bottom: 1px solid #ddd;
	}
	.widget_newpost,
	.widget_popular {
		time {
			font-family: $font-google;
			color: #685143;
		}
		.title {
			color: #926a4a;
			// color: $primary-color;
		}
	} // widget_newpost,widget_popular
	.widget_search {
		input[type="text"] {
			background-color: #e2dad6;
			&:focus {
				color: #685143;
			}
		}
		button[type="submit"],
		input[type="submit"] {
			color: #fff;
			background-color: #685143;
		}
	} // .widget_search
	.widget_archive {
		li {
			a {
				// color: $primary-color;
			}
			.tgl {
				// color: $primary-color;
				i {
					color: #a0928a;
				}
			}
		}
	}

	.tagcloud {
		& > a {
			color: #a38976;
			background-color: transparent;
			border: 1px solid #ccbdb2;
		}
	}
}
