// Staff Archives ---*/
.staff-loop {
	.item {
		article {
			background-color: #fcf7f2;
		}
	}
	.manage {
		color: #6b5345;
	}
	.staff-name {
		a {
			color: #6b5345;
		}
	}
}


// Staff Single ---*/
.staff-header {
	.manage {
		& > span {
			color: #a38976;
			border: 1px solid #ccbdb2;
		}
	}
}
.staff-table {
	th,td {
		border-bottom: 1px solid #e2dad6;
	}
	th {
		color: #a98f7f;
	}
}
.staff-comment {
	background-color: #fcf7f2;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
}
@media (min-width: $screen-tab) {
	.staff-header {
		.title {
			border-bottom: 1px solid #ddd;
		}
	}
}

// Other Staff ---*/
.other-staff {
	background-color: #fcf7f2;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
	.title {
		color: #645042;
		.en {
			font-family: $font-google;
		}
	}
}
.other-staff-list {
	.item {
		color: #645042;
	}
	.manage {
		background-color: transparent;
		border: 1px solid #ccbdb2;
	}
	.staff-list-next,
	.staff-list-prev {
		color: $default-text-color;
		background: $default-color;
	}
}
