.stylist-category {
	background-color: #f7f4ec;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
	border: 1px solid lighten(#644f44, 18%);
	.title {
		color: #644f44;
		border-bottom: 1px solid lighten(#644f44, 18%);
	}
}

// Catalog Search ---*/
.catalog-search-wrapper {
	background-color: #f7f4ec;
}
.catalog_search {
	.cr {
		label {
			background-color: #fff;
		}
		input {
			&:checked {
				& + label {
					color: $btn-default-color;
					background-color: $btn-default-bg-color;
				}
			}
		}

	}
}

// Catalog LoopContent ---*/
.catalog-loop {
	.item {
		background-color: #f7f4ec;
		box-shadow: 0 1px 3px 0px rgba(#333, .2);
		transition: all 100ms ease-in;
		&:hover {
			opacity: .8;
		}
		.not-thumb {
			background-color: rgba(darken(#f7f4ec, 2%), .3);
		}
		.border-catalog {
			border-top: 1px solid #e2cad6;
		}
		.style-name {
			a {
				color: #675042;
			}
		}
		.meta {
			.manage {
				span {
					color: #a38976;
					border: 1px solid #ccbdb2;
				}
			}
			.staff {
				// font-size: 16px;
			}
		}
	}
}


// Single ---*/
.catalog-picture {
	&-body {
		.pic-wrap {
			background-color: #fafafa;
		}
	}
	&-list {
		&-item {
			&.active {
				border-color: $default-color;
			}
		}
	}
	.not-picture {
		color: rgba(#fff, .3);
		background-color: rgba($default-color, .8);
	}
}

// Catalog Content ---*/
.catalog-header {
	.title {
		border-bottom: 1px solid #ddd;
	}
	.comment {
		background-color: #fcf7f2;
		background-repeat: repeat;
		background-position: center;
		background-image: url(dir('common/bg_01.png'));
		&:after {
			border-left: 14px solid #fcf7f2;
		}
	}
	.staff-pic {
		.thumb {
			background-color: rgba(darken(#fcf7f2, 2%), .3);
		}
	}
}

.catalog-table {
	th,td {
		border-bottom: 1px solid #e2dad6;
	}
	th {
		color: #a38976;
	}
}
.catalog-tag {
	& > li {
		a {
			color: #a38976;
			background-color: transparent;
			border: 1px solid #ccbdb2;
		}
	}
}
.catalog-price {
	.price {
		font-family: $font-google;
		color: #695142;
		.numeric {
			color: #695142;
		}
	}
}

.catalog-footer {
	.other-title {
		font-family: $font-google;
		color: #644f44;
		border-bottom: 1px solid #ccbdb2;
	}
}
.other-catalog {
	.swiper-slide {
		a {
			background-color: rgba(#ccbdb2, .3);
		}
		.not-thumb {
			background-color: rgba(#ccbdb2, .3);
		}
	}
}


