.salon-name {
	border-bottom: 1px solid #e2dad6;
	.en {
		color: #695143;
	}
	.jp {
		color: #644f44;
	}
}

.salon-info-table {
	th {
		color: #a0928a;
	}
	th, td {
		border-bottom: 1px solid #e2dad6;
	}
}

.salon-photo-map {
	.item {
		background-color: #fcf7f2;
	}
	.image-map {
		border: 1px solid #e2dad6;
	}
}

.salon-access-flex {
	border-bottom: 1px solid #e2dad6;
}

.salon-access-route {
	color: #685042;
	background-color: #e2dad6;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
}
