// パンくずリスト ---*/
.breadcrumb {
	background-color: transparent;
	border-top: 1px solid #e2dad6;
	border-bottom: 1px solid #e2dad6;
}

.bread-list {
	& > li {
		&:before {
			content: "\f105";
			position: absolute;
			left: 20px;
			line-height: 1;
			font-family: "FontAwesome";
			font-size: 18px;
		}
		&:first-child {
			&:before {
				display: none;
			}
		}
	}
}

// Header ---*/
.header-top {
	overflow: hidden;
	background-color: rgba(#fdfaf5, .5);
}
.site-title {
	font-size: 10px;
	color: $header-title-color;
}
.site-logo {
	a {
		&:hover {
			opacity: .8;
		}
	}
}
@media (max-width: $screen-sp) {
	.site-title {
		background-color: $header-title-bg-color;
	}
}
@media (min-width: $screen-tab) {
	.site-title {
		font-size: 11px;
	}
}

.subnavi {
	a {
		font-size: 14px;
		color: $header-subnavi-color;
		// background-color: $header-subnavi-bg-color;
		background-color: transparent;
		// border: 1px solid $header-subnavi-border-color;
		border: none;
		span {
			display: inline;
		}
		&:hover {
			color: darken($header-subnavi-color, 16%);
			text-decoration: underline;
		}
	}
	i {
		font-size: 126%;
		color: $header-subnavi-icon-color;
	}
}

.drawer-content-back > header {
	background-color: $main-bg;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
	box-shadow: 0 2px 5px 0 rgba(#000, .2);
}
// Header Contact ---*/
.header-contact {
	.btn-default {
		background-image: url(dir('common/bg_brown.png'));
	}
}

// #spContact ---*/
.contact-sp {
	.contact {
		background-color: $contact-sp-bg-color;
		& > li {
			&:nth-child(2) {
				border-left: 1px solid $contact-sp-border-color;
			}
			& > a {
				color: $contact-sp-color;
			}
		}
	}
}

// Footer ---*/
#footer {
	color: #f1eae6;
	background-color: #766156;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_brown.png'));
	a {
		color: #f1eae6;
	}
	.footer-link {
		.btn-default {
			color: #7a6656;
			background-color: #d7cac0;
			background-repeat: repeat;
			background-position: center;
			background-image: url(dir('common/bg_01.png'));
		}
	}
}
.footer-navi-wrap {
	.foot-navi {
		& > li {
			a {
				&:hover {
					border-bottom: 1px dotted #f1eae6;
				}
			}
		}
	}
}
// Footer Social Button ---*/
.footer-social-button-wrap {
	background-color: #fcf7f2;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
	border-top: 1px solid $footer-social-btn-border-color;
	border-bottom: 1px solid $footer-social-btn-border-color;
	.pagetop {
		&.btn-shark {
			background: rgba($btn-default-bg-color, .9);
			border: 1px solid transparent;
			&:hover {
				opacity: .8;
			}
		}
		.fa {
			color: $footer-social-btn-icon-color;
		}
	}
}


// Footer Contact ---*/
.footer-contact-wrap {
	background-color: transparent;
	// background-color: $footer-contact-bg-color;
	border-bottom: 1px solid $footer-contact-border-color;
}

// Footer Navi ---*/
.copyright {
	color: $copyright-color;
}



