.en {
	font-family: $font-google;
}
.c-title {
	margin-bottom: 24px;
	padding: 10px 0;
	border-top: 1px solid #b3a59a;
	border-bottom: 1px solid #b3a59a;
	@media (max-width: $screen-tab - 1) {
		margin-left: -15px;
		margin-right: -15px;
	}
	.en {
		margin: 0;
		font-size: 28px;
		&:before, &:after {
			content: "";
			display: inline-block;
			vertical-align: middle;
		}
		&:before {
			content: url(dir('common/tlt_before.png'));
			margin-right: 24px;
		}
		&:after {
			content: url(dir('common/tlt_after.png'));
			margin-left: 24px;
		}
	}
	.jp {

	}
}
.c-title-sm {
	.en {
		font-size: 28px;
	}
}
.c-title-page {
	.en {
		position: relative;
		font-size: 24px;
		&:before,
		&:after {
			content: "";
			display: inline-block;
			width: 38px;
			height: 20px;
			vertical-align: middle;
			background-repeat: no-repeat;
			background-position: center;
			background-size: 100% auto;
		}
		&:before {
			background-image: url(dir('page/tlt_before.png'));
			margin-right: 24px;
		}
		&:after {
			background-image: url(dir('page/tlt_after.png'));
			margin-left: 24px;
		}
	}
	.jp {
		font-size: 14px;
		color: #644f44;
	}
	@media (min-width: $screen-tab) {
		.en {
			padding-left: 40px;
			padding-right: 40px;
			font-size: 40px;
			&:before, &:after {
				width: 68px;
				height: 30px;
			}
		}
	}
}
