.topics-archive {
	border-bottom: 1px solid #e2dad6;
}
.topics-header {
	.title {
		a {
			color: #685042;
		}
	}
}
.topics-content {
	.datetime {
		font-family: 'Lora', serif;
		font-size: 18px;
	}
	@media (max-width: $screen-tab - 1) {
		.content {
			&:after {
				background: transparent;
			}
		}
	}
}
