.post-archive {
	border-bottom: 1px solid #e2dad6;
	.title {
		a {
			color: #685143;
		}
	}
	.not-thumb {
		background: #f4f1ec;
	}
	.cat {
		color: #a38976;
		background-color: transparent;
		border: 1px solid #ccbdb2;
		a {
			color: #a38976;
		}
	}
	.datetime {
		font-family: $font-google;
		color: lighten(#685143, 5%);
	}
	.content {
		color: #685143;
		&:after {
			background: none;
		}
	}
	@media (max-width: $screen-tab - 1) {
		background-color: #fff;
		box-shadow: 0 1px 2px 1px rgba(#454545, .2);
		.post-link {
			&:hover {
				background-color: #fcfcfc;
			}
		}
		.title {
			&:after {
				@include sgradient(
					$colors:(
						(rgba(#fff, 0), 0%),
						(rgba(#fff, 0), 50%),
						(rgba(#fff, 0), 51%),
						(rgba(#fff, 1), 100%)
					)
				);
			}
		}
	}
}

.post-single {
	.post-header {
		// border-bottom: 1px solid #ddd;
	}

	.datetime {
		font-family: $font-google;
		font-size: 16px;
		color: lighten(#685143, 5%);
	}
	.cat {
		color: #a0928a;
		a {
			font-size: 12px;
			color: #a38976;
			background-color: transparent;
			border: 1px solid #ccbdb2;
		}
	}
	@media (min-width: $screen-tab) {
		.datetime {
			font-size: 20px;
		}
	}
}
.social-count {
	.fa-facebook, .fa-twitter {
		color: #a0928a;
	}
	.count {
		color: #a0928a;
	}
}

.single-pager {
	border-top: 1px solid #ccc;
	a {
		color: #fff;
		background-color: #ccc0b6;
		background-repeat: repeat;
		background-position: center;
		background-image: url(dir('common/bg_02.png'));
		border: 1px solid #ccc0b6;
	}
}
