$drawer-width: 220px;
// Drawer Navigation ---*/
@media (max-width: $screen-tab) {
  #drawerOverlay {
    background-color: rgba(#222, .7);
  }
}

#drawer {
  background-color: #fff;
  background-repeat: repeat;
  background-position: center;
  background-image: url(dir('common/bg.png'));
}


// ドロワーメニュー用のボタン
.drawer {
  &-btn {
    font-family: 'Lora', serif;
    color: #f1eae6;
    text-shadow: 0 -1px 1px rgba(darken(#695143, 5%), .8);
    background-color: rgba(#695143, .7);
    border: 1px solid darken(#695143, 2%);
    &:hover {
      background-color: rgba(darken(#695143, 5%), .7);
    }
  }
  &-btn-close {
    background-color: darken($navi-color-mode, 2%);
    border-left-color: $navi-color-mode;
    &:hover {
      background-color: #922;
    }
  }
}
// ドロワーメニュー
.drawer {
  &-navi {
    background-color: #f4f1ec;
    &:before {
      background-color: #f4f1ec;
    }
    .logo-area {
      color: #fff;
      @include sgradient(
        $colors: (
          (lighten($navi-color-mode, 10%), 0%),
          ($navi-color-mode, 100%)
        )
      );
      border-bottom: 1px solid #999;
    }
  }
  &-inner {
    background-color: #f4f1ec;
  }
}
.drawer-gnavi {
  & > li {
    &.current-menu-item {
      color: #f1eae6;
      background-color: lighten(#695143, 10%);
      > a {
        color: #f1eae6;
        > .ja {
          color: #f1eae6;
        }
        > .en {
          color: #f1eae6;
        }
      }
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 0;
        height: 100%;
        // border: 8px solid transparent;
        // border-top-color: $navi-color-mode;
        // border-left-color: $navi-color-mode;
        border-left: 4px solid $navi-color-mode;
      }
    }
    & > a {
      color: $navi-color-jp;
      border-bottom: 1px solid #ccc;

      > .ja {
        color: $navi-color-jp;
      }
      > .en {
        font-family: $font-google;
        color: $navi-color-en;
      }

      &.hover {
        color: #e6e6e6;
        background-color: #222;
        > .ja {
          color: #e6e6e6;
        }
        > .en {
          color: #e6e6e6;
        }
      }
    }
  }
} // ul.drawer-gnavi

.drawer-subnavi {
  & > li {
    border-bottom: 1px solid lighten(#695143, 10%);
    & > a {
      color: #f1eae6;
      background-color: #695143;
      &:hover {
        background-color: #695143;
      }
    }
  }
}

// グローバルナビゲーション ---*/
.gnavi-block {
  margin-bottom: 0;
}
.gnavi {
  & > li {
    a {
      padding: 16px 0 16px;
      color: $navi-color-mode;
    }
    &:last-child {
      & > a {
        &:after {
          border-right: 1px solid $navi-color-line;
        }
      }
    }
    &.current-menu-item, &:hover {
      &:before {
        content: "";
        position: absolute;
        // top: 0;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 0;
        // border: 8px solid transparent;
        // border-top-color: $navi-color-mode;
        // border-left-color: $navi-color-mode;
        border-bottom: 2px solid $navi-color-mode;
      }
    }
    & > a {
      color: $navi-color-jp;

      > .ja {
        color: $navi-color-jp;
      }

      > .en {
        font-family: $font-google;
        color: $navi-color-en;
      }
      &:hover {
        background-color: transparent;
      }
      &:after {
        border-left: 1px solid $navi-color-line;
      }
    }
  }
}
