// .front-info ---*/
.front-info {
	background-color: #fcf7f2;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_01.png'));
}

// Concept ---*/
.concept {
	&:after {
		content: url(dir('common/img_concept.png'));
		display: block;
		padding-top: 60px;
		text-align: center;
	}
}

// Freespace ---*/
.freespace {
	box-shadow: 0 3px 5px 0 rgba(#000, .2);
	figcaption {
		color: #f4f1ec;
		background-color: rgba(#6b5345, .7);
	}
}

// Blog ---*/
.front-blog {
	.thumb {
		color: #644f44;
		background-color: #fcf7f2;
		.not-thumb {
			&:before {
				color: lighten(#644f44, 12%);
			}
		}
	}
	.datetime {
		color: #644f44;
	}
}
@media (max-width: $screen-tab - 1) {
	.front-blog {
		background-color: #fcf7f2;
		box-shadow: 0 2px 2px 0 rgba(#333, .2);
	}
}
// Catalog ---*/
.front-catalog-wrap {
	.btn-catalog-info {
		color: #fcf7f2;
		background-color: #c3a59a;
	}
	.overlay {
		background-color: rgba(#fff, .6);
		&:before {
			content: "";
			position: absolute;
			right: 0;
			bottom: 0;
			display: block;
			width: 0;
			height: 0;
			border: 15px solid transparent;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.title {
		color: #222;
	}
	.stylist {
		color: #222;
	}
	.swiper-slide {
		background: #f0f0f0;
	}

	$border-width: 2px;
	@media (min-width: $screen-tab + 1) {
		.swiper-button-prev,
		.swiper-button-next {
			&:before {
				border-top: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-prev {
			&:before {
				border-left: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-next {
			&:before {
				border-right: $border-width solid $swiper-border-color;
			}
		}
	}
}


// Front Social ---*/
.instagram {
	& > li {
		background-color: #fff;
		box-shadow: 0 2px 3px 1px rgba(#222, .3);
		time {
			color: #555;
		}
	}
}
.social-heading {
	margin-bottom: 5px;
	font-size: 20px;
	// border-bottom: 1px solid #ddd;
	border: none;
}
